const weather = {
  apiKey: 'fc8448ecbdfb7ff960c2d74a2a385fdc',
  fetchWeather(city) {
    fetch(
      `https://api.openweathermap.org/data/2.5/weather?q=${
        city
      }&units=metric&appid=${
        this.apiKey}`,
    )
      .then((response) => response.json())
      .then((data) => this.displayWeather(data));
  },

  displayWeather(data) {
    const { name } = data;
    const { icon, description } = data.weather[0];
    const { temp, humidity } = data.main;
    const { speed } = data.wind;
    document.querySelector('.city').innerText = `Weather in ${name}`;
    document.querySelector('.icon').src = `https://openweathermap.org/img/wn/${icon}.png`;
    document.querySelector('.description').innerText = description;

    const toCels = document.querySelector('#toCels');
    const toFahr = document.querySelector('#toFahr');

    toCels.addEventListener('click', () => {
      document.querySelector('.temp').innerText = `${Math.floor(temp)}°C`;
    });

    toFahr.addEventListener('click', () => {
      document.querySelector('.temp').innerText = `${Math.floor(temp * (9 / 5) + 32)}°F`;
    });

    document.querySelector('.humidity').innerText = `Humidity: ${humidity}%`;
    document.querySelector('.wind').innerText = `Wind speed: ${speed} km/h`;
    document.querySelector('.weather').classList.remove('fetching');
    document.body.style.backgroundImage = `url('https://source.unsplash.com/random/1920x1080/?${name}')`;
  },

  search() {
    this.fetchWeather(document.querySelector('.search-bar').value);
  },
};

document.querySelector('.search button').addEventListener('click', () => {
  weather.search();
});

document.querySelector('.search-bar').addEventListener('keyup', (e) => {
  if (e.key === 'Enter') {
    weather.search();
  }
});

weather.fetchWeather('Harare');
